import React from 'react';
import "slick-carousel/slick/slick.css";


import Slider from "react-slick";

export default function Slice() {
   let cardes=[  <img src={"https://i.ibb.co/zXLTvth/sfjg9R8.png"} alt="sfjg9R8" />,
 <img src={"https://i.ibb.co/877g8p7/96OnkX7.png" }alt="96OnkX7" />,
 <img  src={"https://i.ibb.co/QmKHp6f/KtGxwnN.png"} alt="KtGxwnN"/>,
 <img src={"https://i.ibb.co/0QkkNk0/p0wdadG.png"} alt="p0wdadG" />

 ]
  const renderSlides = () =>
    cardes.map(num => (
      <section>
        {num}
      </section>
    ));

  return (
    <div >
      <Slider
      className= "center"
      centerMode={true}
      slidesToShow={ 1}
      slidesToScroll={ 1}
      autoplay={ true}
      autoplaySpeed={ 2000}
      dots={ true}
      infinite= {true}
      arrows={ false}
      responsive={ [
        {
          breakpoint: 768,
          settings: {
            arrows: false,
            centerMode: true,
            centerPadding: '40px',
            slidesToShow: 1
          }
        },
        {
          breakpoint: 480,
          settings: {
            arrows: false,
            centerMode: true,
            centerPadding: '40px',
            slidesToShow: 1
          }
        }
      ]}
        
      >
        {renderSlides()}
      </Slider>
    </div>
  );
}
 
  
    
 
