import React from "react";

const Card = (props) => {
  const {
    id,
    name,
    preview,
    photos,
    description,
    size,
    isAccessory,
    brand,
    price,
    addProduct,
  } = props;

  return (
           
        
               <div className="product-card" key={id}>
              <img className="product-image" src={preview} alt={name+"pic"}/>
              <div className="product-meta">
                <h4>{name}</h4>
                <h5>{brand}</h5>
                <p>{"Rs "+price}</p>
              </div>
            </div>
          
        
  );
};
export default Card;
